import React, { memo } from 'react'
import { ContentWrapper } from './style'
import { useLocation } from 'react-router-dom'
import { capitalizeWords } from '@/utils'
import SendForm from '@/components/send-form'
import PrevNext from '@/components/prev-next'
import ShareBtns from '@/components/share-btns'

const ProductContent = memo(() => {
  // 路由参数
  const location = useLocation()
  const { params } = location.state
  const { url, title, des, parentUrl, prev, next } = params
  // 表格参数
  const tableData = {
    "OE NUMBER": "LN003410",
    "MODEL": "SH450-3/-5 SH460HD-5",
    "Warranty": "12 months Warranty",
    "MOQ": "10PCS",
    "Core size": "PA1035*445*125",
  }
  // 返回表格元素
  const tableEl = () => {
    let el = ""
    for (const key in tableData) {
      console.log(key);
      el += `<tr><td>${key}</td><td>${tableData[key]}</td></tr>`
    }
    return el
  }
  return (
    <ContentWrapper>
      <div className="content_wrap">
        <div className="img_title">
          <div className="img_wrap">
            <img src={require('@/assets/data/' + parentUrl + '/' + url)} alt="" />
          </div>
          <div className="title_wrap">
            <p className="title_txt">{title}</p>
            <p className="des_txt">{des}</p>
            <ShareBtns />
            <button className="inquiry_btn">Send Inquiry</button>
          </div>
        </div>
        <div className="introduction">
          <h3 className="intro_tit">Product Introduction</h3>
          <div className="pro_intro">
            <div className="intro_item">
              <div className="pro_tit">{title}</div>
              <p className="intro_content_txt">{des}</p>
            </div>

            <div className="pro_video">
              <video width={560} height={315} controls>
                <source src="https://www.youtube.com/embed/kxRliT0P0nU" type="video" />
              </video>
            </div>

            <div className="intro_item">
              <div className="item_tit">Product Introduction</div>
              <div className="intro_img">
                <img src={require('@/assets/data/' + parentUrl + '/' + url)} alt="" />
              </div>
            </div>

            <div className="intro_item">
              <div className="item_tit">{title.split(" ")[0]} {capitalizeWords(parentUrl)} Specification</div>
              <div className="intro_table">
                <table border={0} cellPadding={0} cellSpacing={0}>
                  <tbody dangerouslySetInnerHTML={{ __html: tableEl() }}>
                  </tbody>
                </table>
              </div>
            </div>

            <p className="hot_tag">
              Hot Tags: {title.toLowerCase()}, China, suppliers, manufacturers, factory, wholesale, buy, price, pricelist, discount, quotation, in stock,
              {title},{des}
            </p>

            <PrevNext prev={prev} next={next} />

            <div className="send_inquiry">
              <h3>Send Inquiry</h3>
              <SendForm />
            </div>
          </div>
        </div>
      </div>
    </ContentWrapper>
  )
})

export default ProductContent